section form {
  display: block;
  margin: 0 auto;
  max-width: 400px; /* ou une autre largeur selon ton design */
  padding: 1rem;
}

section form input, section form textarea {
  display: block;
  width: 100%;
  margin: 0.5rem 0;
  padding: 0.5rem;
}

section form button[type=submit]{
    display: block;
    margin: 1rem auto;
    padding: 1rem;
    font-family: 'Segoe UI';
    font-weight: 600;
    font-size: 1rem;
    background-color: lime;
    border: none;
}

section h2 {
  text-align: center;
}
